
.zpore.connected {
    .nft-jacker {
        z-index: unset !important;
    }
    .drop-top {
        display: block;
        z-index: 1000000000 !important;
    }
}

.zpore {

    .simple-controls {
        position: absolute;
        bottom: 45px;
        left: 45px;
        z-index: 10000000000;

        .play-btn-wrapper {
            border: none;
            background: none;
            cursor: pointer;
        }
    }
    .drop.nft-jacker.jacked.thugger  {
        padding-bottom: unset;
        overflow-y: unset !important;
        .drop-description {
            //display: block !important;
        }
    }
    .drop-top {
        display: none;
    }
}

@media screen and (max-width: 600px) {
    .next-gen.zpore {
        .simple-controls {
            position: absolute;
            bottom: 0px;
            left: unset;
            right: -15px;
        }
    }
}

